import { Component, OnInit } from '@angular/core';
import { Feedback, FeedbackDialogComponent } from '../feedback-dialog/feedback-dialog.component';
import { SnackService } from '../../services/snack.service';
import { FeedbackService } from '../feedback.service';
import { DialogService } from '../../services/dialog.service';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class FeedbackComponent implements OnInit {

  constructor(
    private dialogService: DialogService,
    private feedbackService: FeedbackService,
    private snackService: SnackService
  ) { }

  ngOnInit(): void {
  }

  feedbackClicked() {
    this.dialogService.openDialog(FeedbackDialogComponent, {
    }).subscribe((result: Feedback) => {
      if (result) {
        this.feedbackService.sendFeedback(result).subscribe(() => {
          this.snackService.displayMessage('Thank you for your feedback !');
        }, error => {
          console.log(error);
          this.snackService.displayMessage('Sorry, something went wrong sending your feedback.');
        });
      }
    });
  }
}
