import { Injectable } from '@angular/core';
import { Feedback } from './feedback-dialog/feedback-dialog.component';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FeedbackService {

  constructor(private httpClient: HttpClient) { }

  sendFeedback(result: Feedback): Observable<any> {
    const data = {
      files: {
        'goal.txt': {
          content: result.goal
        },
        'features.txt': {
          content: result.features
        },
        'other.txt': {
          content: result.other
        },
        'mail.txt': {
          content: result.mail
        },

      },
      description: 'feedback',
    }
    return this
      .httpClient
      .post(
      'https://api.github.com/gists?access_token=' + environment.feedbackToken,
        data
    );
  }
}
