import {Injectable, TemplateRef} from '@angular/core';
import {MatDialog, MatDialogConfig} from '@angular/material/dialog';
import {ComponentType} from '@angular/cdk/overlay';
import {Observable} from 'rxjs';
import { BreakpointService } from './breakpoint.service';

@Injectable()
export class DialogService {
  isOpened = false;

  constructor(
    private readonly breakpointService: BreakpointService,
    public dialog: MatDialog,
  ) { }


  openDialog(component: ComponentType<any> | TemplateRef<any>, config?: MatDialogConfig): Observable<any> {
    if (this.isOpened) {
      return;
    }
    this.isOpened = true;

    let dialogConfig: MatDialogConfig = {
      width: 'calc(100% - 50px)',
      maxWidth: '100vw',
      panelClass: 'no-border-radius',
    }
    if (config) {
      dialogConfig = {
        ...dialogConfig,
        ...config,
      };
    }
    const d = this.dialog.open(component, dialogConfig);

    const smallDialogSubscription = this.breakpointService.isExtraSmall.subscribe(size => {
      if (size.matches) {
        d.updateSize('100vw', '100vh');
      } else {
        d.updateSize('calc(100% - 50px)', '');
      }
    });

    const afterClosed = d.afterClosed();
    afterClosed.subscribe(() => {
      this.isOpened = false;
      smallDialogSubscription.unsubscribe();
    });
    return afterClosed;
  }
}
